<template>
  <div class="card">
    <div class="card-header">Exam02Watch</div>
    <div class="card-body">
      <div class="form-group row">
        <label class="col-sm-2 col-form-label">UserId</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="userId" />
        </div>
      </div>
      <hr />
      <form>
        <div class="form-group row">
          <label class="col-sm-2 col-form-label">Name</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" v-model="product.name" />
          </div>
        </div>
        <div class="form-group row">
          <label class="col-sm-2 col-form-label">Company</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" v-model="product.company" />
          </div>
        </div>
        <div class="form-group row">
          <label class="col-sm-2 col-form-label">Price</label>
          <div class="col-sm-10">
            <input type="number" class="form-control" v-model.number="product.price" />
          </div>
        </div>
      </form>
      <hr />
      <button @click="handleButton" class="btn btn-info btn-sm">product 객체 변경</button>
    </div>
  </div>
</template>

<script setup>
import { ref, watch } from "vue";

//반응형 속성 선언
const userId = ref("");
const product = ref({
  name: "",
  company: "",
  price: 0,
});

//값인 속성 감시 설정
watch(userId, (newUserId, oldUserId) => {
  console.group("userId 변경 감시");
  console.log("newUserId : " + newUserId);
  console.log("oldUserId : " + oldUserId);
  console.groupEnd();
});

//객체 타입의 속성 감시 설정[객체 자체가 변경될 때만 감시하고 내부 속성 변경 시에는 감시 안함]
watch(
  product,
  (newProduct, oldProduct) => {
    console.group("Product변경 감시");
    console.log("newProduct : ", newProduct);
    console.log("oldProduct : ", oldProduct);
    console.groupEnd();
  },
  { deep: true }, //이 친구가 있으면 값이 변경될 때마다 감시해줌
);

//객체 타입의 속성 일부 감시 설정
//앞에 함수로 리턴값이 변경된 것을 확인하면 뒤에 함수가 실행됨
watch(
  () => product.value.name,
  (newName, oldName) => {
    console.group("Product 객체의 name 속성 변경 감시");
    console.log("newName : ", newName);
    console.log("oldName : ", oldName);
    console.groupEnd();
  },
);

//멀티 반응형 속성 감시 설정
watch([userId, product], ([newUserId, newProduct], [oldUserId, oldProduct]) => {
  console.group("멀티 반응형 속성 변경 감시");
  console.log("new: ", [newUserId, newProduct]);
  console.log("old: ",  [oldUserId, oldProduct]);
  console.groupEnd();
},
  { deep: true }, //이 친구가 있으면 값이 변경될 때마다 감시해줌 
);

//새로운 객체로 바꾸는 함수
function handleButton() {
  product.value = {
    name: "",
    company: "",
    price: 0,
  };
}
</script>

<style scoped></style>
